<template>
  <div id="home-component" class="container-fluid" style="background: #e3e5eb;">
    <!-- alumno -->
    <div class="row" style="background: #e3e5eb;">
      <div class="col-lg-12 pt-0">
        <div class="card w-100">
          <div class="container mt-3">
            <h5>Detalles Alumno</h5>
            <hr />
            <v-form v-on:submit.prevent="saveAlumno" ref="form" v-model="validForm" enctype="multipart/form-data">
              <div class="row">
                <div class="col-lg-12">
                  <div class="overline">DATOS DEL ALUMNO</div>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12">
                  <v-text-field
                    outlined
                    @keyup="consultarDNI('hijo')"
                    v-model="user.dni"
                    id="cdt-1"
                    label="DNI *"
                  ></v-text-field>
                  <v-text-field id="cdt-18" outlined v-model="user.nombres" label="Nombre *"></v-text-field>
                  <v-text-field
                    id="cdt-2"
                    outlined
                    v-model="user.apellido_paterno"
                    label="Apellido Paterno *"
                  ></v-text-field>
                  <v-text-field
                   id="cdt-3"
                    outlined
                    v-model="user.apellido_materno"
                    label="Apellido Materno *"
                  ></v-text-field>
                  <v-menu
                    ref="menu"
                    v-model="menu"
                    :close-on-content-click="false"
                    transition="scale-transition *"
                    offset-y
                    min-width="290px"
                  >
                    <template v-slot:activator="{ on }">
                      <v-text-field
                         id="cdt-4"
                        outlined
                        v-model="user.fecha_nacimiento"
                        label="Fecha de Nacimiento *"
                        readonly
                        v-on="on"
                      ></v-text-field>
                    </template>
                    <v-date-picker
                      ref="picker"
                      v-model="user.fecha_nacimiento"
                      :max="new Date().toISOString().substr(0, 10)"
                      min="1950-01-01"
                      @change="saveDate"
                    ></v-date-picker>
                  </v-menu>
                  <v-text-field id="cdt-5" outlined v-model="user.direccion" label="Domicilio *"></v-text-field>
                  <v-text-field id="cdt-6" outlined v-model="user.telefono" label="Telefono Casa *"></v-text-field>
                  <v-text-field 
                  id="cdt-7"
                  outlined 
                  v-model="user.password" 
                  label="Contraseña *"
                  :type="show1 ? 'text' : 'password'"
                  @click:append="show1 = !show1"
                  :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                  ></v-text-field>

                  <v-switch
                  class="mx-2"
                    id="cdt-20"
                    v-model="user.activo"
                    @change="cambiaMensajeActividad"
                    :label="`Activo: ${mensajeActividad}`"
                    ></v-switch>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12">
                  <v-combobox
                    id="cdt-8"
                    outlined
                    v-model="user.nivel"
                    @change="updateListGrados"
                    :items="basecolegio.niveles"
                    label="Nivel *"
                  ></v-combobox>

                  <v-combobox id="cdt-9" outlined v-model="user.grado" :items="grados" label="Grado *"></v-combobox>

                  <v-file-input
                    v-model="user.imagen"
                    accept="image/png, image/jpeg, image/bmp, image/jpg"
                    placeholder="Click aqui para elegir una nueva imagen.."
                    prepend-icon
                    prepend-inner-icon="mdi-camera"
                    label="Imagen"
                    id="cdt-19"
                    outlined
                    :loading="loadingImage"
                    @change="updatePreview()"
                  ></v-file-input>
                  <div
                    class="card w-100 mx-auto d-flex flex-column flex-nowrap align-items-center justify-content-center"
                    style="border:none;"
                  >
                    <img
                      
                      id="img-preview"
                      src="../../../assets/img/no-image.png"
                      alt="img-preview"
                      class="img-fluid"
                      style="max-height: 340px; max-width: 340px;  border-radius: 50%;"
                    />
                  </div>
                </div>
              </div>
              <!-- parent -->
              <div class="row">
                <div class="col-lg-12">
                  <div class="overline">DATOS DEL APODERADO</div>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12">
                  <v-text-field
                     id="cdt-10"
                    @keyup="consultarDNI('padre')"
                    v-model="user.pariente.dni"
                    outlined
                    label="DNI"
                    required
                  ></v-text-field>
                  <v-text-field id="cdt-11" v-model="user.pariente.nombres" outlined label="Nombres" required></v-text-field>
                  <v-text-field
                    id="cdt-12"
                    v-model="user.pariente.apellido_paterno"
                    outlined
                    label="Apellido Paterno"
                    required
                  ></v-text-field>
                  <v-text-field
                    id="cdt-13"
                    v-model="user.pariente.apellido_materno"
                    outlined
                    label="Apellido Materno"
                    required
                  ></v-text-field>
                </div>
                <div class="col-lg-6 col-md-12 col-sm-12">
                  <v-text-field
                    id="cdt-14"
                    v-model="user.pariente.direccion"
                    outlined
                    label="Domicilio"
                    required
                  ></v-text-field>
                  <v-text-field id="cdt-15" v-model="user.pariente.correo" :rules="emailRules" outlined label="E-mail" required></v-text-field>
                  <v-text-field id="cdt-16" v-model="user.pariente.telefono" outlined label="Telefono" required></v-text-field>
                  <v-text-field id="cdt-17" v-model="user.pariente.celular" outlined label="Celular" required></v-text-field>
                </div>
              </div>
              <div class="row">
                <div class="col-lg-12">
                  <div class="d-flex flex-row flex-wrap justify-content-between">
                      <v-btn @click="retornar" color="primary"><i class="ml-auto material-icons mr-2">keyboard_arrow_left</i>Regresar</v-btn>
                      <v-btn v-if="habilitado" @click="habilitarEdit" color="primary"><i class="ml-auto material-icons mr-2">cached</i>Habilitar editado</v-btn>
                      <v-btn v-else @click="deshabilitarEdit" class="text-white" color="#C62828"><i class="ml-auto material-icons mr-2">cached</i>Deshabilitar editado</v-btn>
                      
                      <div>
                        <v-dialog v-model="dialog" width="500">
                            <template v-slot:activator="{ on }">
                                <v-btn v-on="on"  color="primary"><i class="ml-auto material-icons mr-2">save</i>Nueva Contraseña</v-btn>
                            </template>
                            <v-card>
                                <v-card-title>
                                <span class="headline">Nueva contraseña</span>
                                </v-card-title>
                                <v-card-text>
                                    <v-container>
                                        <v-row>
                                            <v-col cols="12" sm="12" md="12">
                                                <v-text-field 
                                                v-model="newPassword" 
                                                label="Nueva Contraseña"
                                                :type="show2 ? 'text' : 'password'"
                                                @click:append="show2 = !show2"
                                                :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                                                ></v-text-field>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" text @click="closePassModal">Cerrar</v-btn>
                                    <v-btn color="blue darken-1" text @click="savePassModal">Guardar</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                      </div>                      
                        <v-btn  color="primary"><i class="ml-auto material-icons mr-2">event_note</i>Ver Avance</v-btn>
                      <v-btn @click="saveEditAlumno" color="primary"><i class="ml-auto material-icons mr-2">save</i>Guardar cambios</v-btn>
                  </div>
                </div>
              </div>
            </v-form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import defaultImg from '../../../assets/img/no-image.png';
import axios from "axios";
import { mapState } from "vuex";
import Swal from 'sweetalert2'

export default {
  data: () => ({
    habilitado: false,
    show1: false,
    show2: false,
    password: 'Password',
    dialog: false,
    validForm: true,
    mensajeActividad: '',
    newPassword: '',
    tokenReniec:
      "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJlbWFpbCI6Imx1aXNtZWxnYXJlam9hbGFyY29uQGdtYWlsLmNvbSJ9.sPuL_WIleq7us1RzH1feIjWG_7NC20Jj_9LBgvpQivU",
    user: {
      nombres: "",
      apellido_paterno: "",
      apellido_materno: "",
      dni: "",
      direccion: "",
      fecha_nacimiento: null,
      edad: "",
      telefono: "",
      nivel: "",
      grado: "",
      imagen: null,
      password: "",
      rol_usuario: "alumno",
      pariente: {
        dni: "",
        nombres: "",
        apellido_paterno: "",
        apellido_materno: "",
        direccion: "",
        correo: "",
        telefono: "",
        celular: ""
      }
    },
    default_user: {
      nombres: "",
      apellido_paterno: "",
      apellido_materno: "",
      dni: "",
      direccion: "",
      fecha_nacimiento: null,
      edad: "",
      telefono: "",
      nivel: "",
      grado: "",
      imagen: null,
      password: "",
      rol_usuario: "alumno",
      pariente: {
        dni: "",
        nombres: "",
        apellido_paterno: "",
        apellido_materno: "",
        direccion: "",
        correo: "",
        telefono: "",
        celular: ""
      }
    },
    urlImage: '',
    grados: "",
    menu: false,
    loadingImage: false,
    rules: [
      value =>
        !value ||
        value.size < 2000000 ||
        "Avatar size should be less than 2 MB!"
    ],
  }),
  computed: {
    ...mapState(["basecolegio"])
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  mounted() {
    /* this.removeImagen(); */
    this.$store.dispatch("getBaseColegio");
    this.$store.dispatch('updateGlobalIndex');
    this.getAlumno();
    this.deshabilitarEdit()
  },
  methods: {
    saveDate(date) {
      this.$refs.menu.save(date);
    },
    async consultarDNI(value) {
      try {
        if (value === "hijo") {
          if (this.user.dni.length >= 8) {
            const url = "http://dniruc.apisperu.com/api/v1/dni/";
            const res = await axios.get(
              url + this.user.dni + "?token=" + this.tokenReniec
            );
            if (res.data) {
              this.user.nombres = res.data.nombres;
              this.user.apellido_paterno = res.data.apellidoPaterno;
              this.user.apellido_materno = res.data.apellidoMaterno;
            }
          }
        } else {
          if (this.user.pariente.dni.length >= 8) {
            const url = "http://dniruc.apisperu.com/api/v1/dni/";
            const res = await axios.get(
              url + this.user.pariente.dni + "?token=" + this.tokenReniec
            );
            if (res.data) {
              this.user.pariente.nombres = res.data.nombres;
              this.user.pariente.apellido_paterno = res.data.apellidoPaterno;
              this.user.pariente.apellido_materno = res.data.apellidoMaterno;
            }
          }
        }
      } catch (error) {}
    },
    openFI() {
      const fileInput = document.getElementById("cdt-19");
      photoInput.click();
    },
    updatePreview() {
      try {
        if (this.user.imagen !== undefined) {
          this.loadingImage = true;
          const reader = new FileReader();
          reader.readAsDataURL(this.user.imagen);
          reader.onload = () => {
            const imgPrev = document.getElementById("img-preview");
            imgPrev.src = reader.result;
            this.loadingImage = false;
          };
        } else {
          this.removeImagen();
        }
      } catch (error) {
        throw error;
      }
    },
    removeImagen() {
      document.getElementById("img-preview").src = this.urlImage;
      this.user.image = undefined;
    },
    updateListGrados() {
      try {
        const arrGrados = [];
        const grados = this.basecolegio.grados;
        grados.forEach(grado => {
          if (grado.nivel === this.user.nivel) {
            arrGrados.push(grado.nombre);
          }
        });
        this.grados = arrGrados;
      } catch (error) {
        return;
      }
    },
    async saveEditAlumno() {
      if(this.verificaCampos()){
          //corregir esto
        let formUser, result;
        if(this.user.imagen !== undefined && this.user.imagen !== null){
            formUser = new FormData();
            this.user.imagen = this.urlImage;
            const userMinified = JSON.stringify(this.user);
            formUser.append('image', this.user.imagen);
            formUser.append('user', userMinified);
            result = await this.$store.dispatch("editAlumno", formUser);
        }else {
            this.user.imagen = this.urlImage;
            result = await this.$store.dispatch("editAlumno", this.user);
        }
        
        if (result){
          this.user.imagen = null;
          Swal.fire(
            'Good job!',
            'El alumno se editó correctamente!',
            'success'
          )
        }
      }else{

        Swal.fire(
          'Datos insuficientes',
          'Debes rellenar como mínimo todos los campos obligatorios que contienen (*)',
          'error'
        )
      }
    },
    verificaCampos() {
      if((this.user.nombres === "") || (this.user.apellido_paterno === "") || (this.user.apellido_materno === "") 
      || (this.user.dni === "") || (this.user.direccion === "") || (this.user.fecha_nacimiento === "") 
      || (this.user.dni === "") || (this.user.password === "")) {
        return false;
      }
      return true;
    },
    async getAlumno(){
        const resUser = await this.$store.dispatch('getAlumnoPorId', this.$route.params.user_id);
        if(!resUser) {
            Swal.fire(
            'Alumno no encontrado!',
            'El alumno buscado no ha sido encontrado.',
            'error'
            )
            return this.$router.push('/app1/aintranet/alumnos/')
        }
        this.user = resUser;
        document.getElementById('img-preview').src = resUser.imagen;
        this.urlImage = resUser.imagen;
        this.user.imagen = null;
        this.cambiaMensajeActividad()
    },
    deshabilitarEdit(){
        this.habilitado = true;
        for(let i=1; i<20; i++){
            const input = document.getElementById(`cdt-${i}`);
            input.disabled = true ;
        }
    },
    habilitarEdit(){
        this.habilitado = false;
        for(let i=1; i<20; i++){
            const input = document.getElementById(`cdt-${i}`);
            if(i!=7){
                input.disabled = false ;
            }
        }
    },
    cambiaMensajeActividad(){
        if(this.user.activo){
            this.mensajeActividad = 'Usuario Habilitado'
        }else {
            this.mensajeActividad = 'Usuario Deshabilitado'
        }
    },
    retornar(){
        this.$router.push('/app1/aintranet/alumnos')
    },
    closePassModal () {
        this.dialog = false
    },

    async savePassModal () {
        const oldPassword = this.user.password;
        this.user.password = this.newPassword;
        const res = await this.$store.dispatch('updatePasswordUser', this.user);
        if(res){
            this.closePassModal();
            Swal.fire(
                'Good Job!',
                `La contraseña se actualizó correctamente, <br> La nueva contraseña es ${this.newPassword}`,
                'success'
            )
        }else{
            this.closePassModal();
            Swal.fire(
                'Oops!',
                'Ocurrio un error y no se pudo actualizar la contraseña.',
                'error'
            )
        }
        this.user.password = oldPassword;
    },
  }
};
</script>

